import React from "react";
import "./App.css";

function App() {
  const colors = ["R", "Y", "G"];
  let secuence = "";
  let curretSecuence = "";

  function fire() {
    curretSecuence = curretSecuence + colors[Math.floor(Math.random() * 3)];
    secuence = "";
    var xhr = new XMLHttpRequest();
    console.log("secuence", curretSecuence);
    xhr.addEventListener("load", () => {
      console.log(xhr.responseText);
    });
    xhr.open(
      "GET",
      "https://5f7789a49449f700070e1767.onlosant.com/gpio/" + curretSecuence
    );
    xhr.send();
  }

  function checkRed(e) {
    e.preventDefault();
    secuence = secuence + "R";
    var ccs = curretSecuence.slice(0, secuence.length);
    if (secuence === ccs) {
      if (secuence.length === curretSecuence.length) {
        fire();
      }
    } else {
      console.log("You lost")
    }
  }

  function checkYellow(e) {
    e.preventDefault();
    secuence = secuence + "Y";
    var ccs = curretSecuence.slice(0, secuence.length);
    if (secuence === ccs) {
      if (secuence.length === curretSecuence.length) {
        fire();
      }
    } else {
      console.log("You lost")
    }
  }

  function checkGreen(e) {
    e.preventDefault();
    secuence = secuence + "G";
    var ccs = curretSecuence.slice(0, secuence.length);
    if (secuence === ccs) {
      if (secuence.length === curretSecuence.length) {
        fire();
      }
    } else {
      console.log("You lost")
    }
  }

  fire();

  return (
    <div className="App">
      <button type="button" onClick={checkRed} >
        RED
      </button>
      <br></br>
      <button type="button" onClick={checkYellow}>
        YELLOW
      </button>
      <br></br>
      <button type="button" onClick={checkGreen}>
        GREEN
      </button>
    </div>
  );
}
export default App;
